body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: Open Sans, sans-serif;
  background: #F5F6F8;
}
.container {
  max-width: 940px;
  margin: auto;
}


@media screen and (max-width: 1024px) {
  .container {
    padding: 0 20px;
  }
}


img {
  width: 100%;
}

.App-header {
  background: -webkit-linear-gradient(top, rgba(7, 92, 64, 0.0001) 75.6%, rgba(7, 92, 64, 0.124153) 99.32%), -webkit-linear-gradient(274.76deg, #A2DDA7 0%, #3FCAAB 100%);
  background: linear-gradient(180deg, rgba(7, 92, 64, 0.0001) 75.6%, rgba(7, 92, 64, 0.124153) 99.32%), linear-gradient(175.24deg, #A2DDA7 0%, #3FCAAB 100%);
  padding: 29px 40px;
  box-shadow: 0px 1px 1px rgba(11, 137, 97, 0.346181);
}

.header-logo {
  width: 50px;
  height: auto;
  margin: auto;
}


.company-info h1{
  margin-top: 50px;
  font-style: normal;
  font-weight: bold;
  line-height: 38px;
  font-size: 1.75em;
  color: #3B4651;
}

.company-info p {
  font-style: italic;
  font-weight: normal;
  line-height: 1.188em;
  font-size: 0.875em;
  color: #3B4651;
  mix-blend-mode: normal;
  opacity: 0.65;
}

.filters-group {
  margin-top: 33px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.filters-group .search-filter{
  width: 286px;
  height: 35px;
  border: 1px solid #BFD1CB;
  box-shadow: inset 0px 2px 5px #F3F3F3;
  border-radius: 5px;
  position: relative;
  background-color: #FFFFFF;
}

.filters-group .search-filter input{
  margin:2px;
  height: 30px;
  width: 230px;
  float: right;
  border-radius: 5px;
  border: none;
  color: #888888;
  font-style: italic;
  font-weight: normal;
  line-height: 1.188em;
  font-size: 0.875em;
  mix-blend-mode: normal;
  opacity: 0.65;
}

.filters-group .search-filter .search-icon {
  width: 16px;
  position: absolute;
  left: 10px;
  top: 10px;
  opacity: 0.5;
}

.filters-group .toggle-filters {
  margin-left: auto;
  align-self: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

.filters-group .toggle-filters span {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 10px;
}

.filters-group .toggle-filters i {
  cursor: pointer;
  display: inline-block;
  width: 32px;
  height: 32px;
  margin-left: 4px;
  background-repeat: no-repeat;
}

.filters-group .toggle-filters i.asc {
  background-image: url(/camera-system/static/media/sort-amount-up.fb7edf77.svg);
}

.filters-group .toggle-filters i.desc {
  background-image: url(/camera-system/static/media/sort-amount-down.c6f1f900.svg);
}

.filters-group .toggle-filters i.none {
  background-image: url(/camera-system/static/media/layer-group-slash.03bc7b67.svg);
}

.filters-group .toggle-filters i.status {
  background-image: url(/camera-system/static/media/layer-group.972803d8.svg);
}

.devices-gallery {
  padding-bottom: 60px;
}


.devices-group {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    grid-gap: 30px;
}

.group-title {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 60px 0 30px 0;
}

.group-title strong{
  padding-right: 1em;
  line-height: 1.375em;
  font-size: 1em;
  letter-spacing: 0.75px;
  color: rgba(24, 22, 22, 0.646798);
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.group-title span {
  font-style: normal;
  font-weight: normal;
  line-height: 19px;
  font-size: 14px;
  letter-spacing: 0.65625px;
  text-transform: uppercase;

  color: rgba(24, 22, 22, 0.646798);

  mix-blend-mode: normal;
  opacity: 0.75;
}

.group-division{
  height: 1px;
  background: #666465;
  mix-blend-mode: normal;
  opacity: 0.35;
  margin-left: 1em;
  width: 940px;
}


.device-card {
  width: 290px;
  background: #FFFFFF;
  box-shadow: 0px 5px 15px rgba(150, 187, 178, 0.23951);
  border-radius: 10px;
}

.inactive-card{
  opacity: 0.50;
}

.device-card img{
  height: 218px;
  border-radius: 10px 10px 0 0;
}

.device-card .card-description {
  padding: 16px 16px 0 16px;
  display: -webkit-flex;
  display: flex;
  color: #2B4039;
}

.device-card .card-description .id-text {
  margin-left: auto;
  -webkit-align-self: center;
          align-self: center;
  font-size: 1.1em;
  padding: 2px 10px;
  mix-blend-mode: normal;
  opacity: 0.75;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}


.device-card .card-description p .status-text {
  font-family: Raleway, sans-serif;
  line-height: normal;
  font-size: .688em;
  letter-spacing: 1px;
  text-transform: uppercase;
  mix-blend-mode: normal;
  opacity: 0.65;
}

.device-card .card-description p .name-text {
  font-family: Lato, sans-serif;
  line-height: normal;
  font-size: 1em;
  letter-spacing: 0.75px;
  mix-blend-mode: normal;
  opacity: 0.85;
}

.device-card .card-description .status-bar {
  height: 40px;
  width: 6px;
  mix-blend-mode: normal;
  opacity: 0.92;
  border-radius: 7.5px;
  -webkit-align-self: center;
          align-self: center;
  margin-right: 10px;
}

.inactive-bar{
  background: #CA6060;
}

.active-bar{
  background: #1DD387;
}

